body {
  text-align: center;
  background-color: #F2F2F2;
}
.App {
  text-align: center;
}

.text-center
{
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 48px;
  line-height: 56px;
  text-align: center;
  color: #77DD55;
}

.form-container {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0px;
  left: 0px;
}
.form-container form {
  flex-basis: 600px;
}
.form-container .form-content {
  padding: 60px;
  border-radius: 3px;
  background: #FFFFFF;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
}
.slick-slide img {
  width: 100%;
  height: 100%;
  max-height: 915px;
}
.slick-prev {
  left: 80px;
  z-index: 100;
}
.slick-next {
  right: 80px;
  z-index: 100;
}
.slick-prev:before, 
.slick-next:before {
  font-size: 35px;
}
.custom-control-input:checked~.custom-control-label::before {
  color: #fff;
  border-color: #157d2d;
  background-color: #28a745;
}
.custom-control-input:focus:not(:checked)~.custom-control-label::before {
  border-color: #157d2d;
  box-shadow: 0 0 0 0.2rem #28a74561;
}
.custom-control-input:not(:disabled):active~.custom-control-label::before {
  background-color: #28a74547;
  border-color: #28a74547;
  
}
.custom-inline-checkbox
{
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
}
.custom-inline-checkbox2
{
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
}
.custom-control-label::before,
.custom-control-label::after {
  top: 0.1rem;
}
.form-control {
  background: #FFFFFF;
  border: 1px solid #CED4DA;
  box-sizing: border-box;
  border-radius: 2.5px;
  border-radius: 3px;
  box-shadow: 1px 1px 2px #B6B6B6;
}
.form-control:focus {
  border-color: #157d2d;
  box-shadow: 0 0 0 0.2rem #28a74561;
}
#trip_plan_btn {
  border-radius: 30px;
  padding-left: 50px;
  padding-right: 50px;
  border: 2px solid #fafafa;
}
.leaflet-container
{
  width: 100%;
  height: 100vh;
}